.card-slider {
  margin: 0 auto;
  max-width: 95%;
  padding: 40px 0;
  
}

.card-wrapper {
 font-family: 'brandon_light';
  width: 80%;
  position: relative;
  cursor: pointer;
  

}

.card {
  width: 80%;
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
  text-align: center;
  
}

.card img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}

.description {
  color: #666;
  font-size: 16px;
  margin: 10px auto;
}

.button {
  border: 1px solid #282828;
  width: 100%;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}



.slick-dots li button:before {
  color: #999;
  font-size: 12px;
}


.block{
  position: absolute;
  bottom: 150px;
  left: 0;
  width: 100%;
  height: 10%;
  background-color: rgba(255, 255, 255, 0.8);
  padding-top: 10px;
  
}